export const getPlaceData = async () => {
  try {
    fetch("/api/hospitals")
      .then((res) => res.json())
      .then((res) => {
        return res;
      })
      .catch((err) => `Can not load specific data type  ${err}`);
  } catch (error) {
    console.log(error);
  }
};
